:root {
  --main-ff: "Inter", sans-serif;
  --second-ff: "RobotoMono", sans-serif;
  --color-dark: #000;
  --color-white: #fff;
  --color-btn-bg: linear-gradient(91.83deg, #095092 0%, #00a7e5 100%);
  --color-light-blue: #00a7e5;
  --color-blue: #0578B9;
  --color-card-grey: #CDCDD1;
  --color-bg: #DDDDE2;
}

@font-face {
  font-weight: 400;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
}

@font-face {
  font-weight: 600;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
}

@font-face {
  font-weight: 400;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Regular.woff2") format("woff2"), url("../fonts/RobotoMono-Regular.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Medium.woff2") format("woff2"), url("../fonts/RobotoMono-Medium.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "RobotoMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Bold.woff2") format("woff2"), url("../fonts/RobotoMono-Bold.woff") format("woff");
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  line-height: 1.3;
}

body {
  color: var(--color-dark);
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.9rem;
  font-family: var(--main-ff);
  background-color: var(--color-bg);
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

.list-rest {
  padding: 0;
  margin: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.site-container {
  width: 100%;
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
}
